import axios from './axios';

// 
export function getNoticeList(option) {
  const url = '/weixin/notice/lists';

  return axios.post(url, option);
}

// 
export function getNoticeDetail(option) {
  const url = '/weixin/notice/detail';

  return axios.post(url, option);
}

// 
export function getNoticePreivew(option) {
  const url = '/weixin/notice/previewDetail';

  return axios.post(url, option);
}

// 
export function getNoticeComment(option) {
  const url = '/weixin/notice/readComment';

  return axios.post(url, option);
}

// 
export function sendNoticeComment(option) {
  const url = '/weixin/notice/comment';

  return axios.post(url, option);
}

// 
export function getNoticeReply(option) {
  const url = '/weixin/notice/readReply';

  return axios.post(url, option);
}

// 
export function sendNoticeReply(option) {
  const url = '/weixin/notice/reply';

  return axios.post(url, option);
}



